.bars-solid{
   width:50px;
   left:20px;
   position:absolute;
}
.chat-host{
  overflow-x: hidden;
  overflow-y: auto;
  top:45px;
  position:fixed;
  height:calc(100% - 108px);
  width:100%;
  background-color:#283965;
  margin-top:4px;
  padding-top:15px;
  padding-bottom:10px;
}
.input-host{
  height:59px;
  width:100%;
  position:fixed;
  bottom:0px; 
  background-color:#283965;
}
.chat-input{
  background-color: #252525;
  color:#ffffff;
  border:none; 
  outline:none; 
  display:inline-block; 
  height:44px; 
  font-size:20px;
  vertical-align:middle; 
  position:fixed; 
  bottom:14px; 
  left:1%;
  border-radius:22px; 
  width:98%; 
  box-sizing:border-box; 
  padding:0 18px; 
}
.chat-input-button{
   background-color: #252529;
  color:#ffffff;
  border:none; 
  outline:none; 
  display:inline-block; 
  height:44px; 
  font-size:20px;
  vertical-align:middle; 
  position:fixed; 
  bottom:14px; 
  right:3%; 
  border-radius:22px; 
  width:auto; 
  box-sizing:border-box; 
  padding:0 18px; 
}
.chat{
  clear:both;
}
.chat-me-host{
  clear:both;
  height:auto;
  float: right;
  text-align:right;
}
.chat-me{
  clear:both;
  background-image: linear-gradient(to right,  #00B2FF , #006AFF);
  color:white;
  width:auto;
  height:auto;
  padding: 15px;
  right:0px;
  border-radius: 20px 0px 20px 20px;
  line-height: 1.5;
  font-size: 14px;
  text-align:right;
  font-weight: 500;
  position:relative;
}
@keyframes sam {
   0%{
     width:0px;
     left:5px;
     font-size:0px;
   }
   50%{
     width:10px;
     font-size:5px;
   }
   100%{
     font-size:17px;
     width:auto;
     left:0;
   }
}
.chat-sam-host{
  clear:both;
  height:auto;
  float: left;
}
.chat-sam{
  width:auto;
  animation-name:sam;
  animation-duration:1s;
  animation-iteration-count: 1;
  background-image: linear-gradient(to right,  #ffffff, #e1e1e1);
  padding: 15px;
  border-radius: 20px 20px 20px 0;
  line-height: 1.5;
  font-size: 14px;
  font-weight: 500;
  left:0;
}
.chat-input-sam{
  background-color: #fff;
  border-radius: 20px 20px 20px 0;
  font-size: 16px;
}

.select-light{
  outline: none;
  border:none;
  width: 180px;
  border-radius: 0px 20px 20px 20px;
  background-image: linear-gradient(to right,  #ffffff, #e1e1e1);
  margin-top:-15px;
  position:absolute;
  clear:both;
}

.Servo-range {
  background-image: linear-gradient(to right,  #ffffff, #e1e1e1);
  
}